/* scss/Atoms/_Toggle.scss */
label.switch {
    position: relative;
    display: inline-block;
    min-width: 50px;
    height: 15px;
    margin: 1em;
    /* Rounded sliders */
}

label.switch input[type="checkbox" i], label.switch input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    opacity: 0;
    width: 0;
    height: 0;
}

label.switch span.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
}

label.switch span.slider:before {
    position: absolute;
    content: "";
    height: 1.5em;
    width: 1.5em;
    left: 0;
    bottom: -.3em;
    box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    transition: .4s;
}

label.switch input[type="checkbox"]:checked + .slider {
    background-color: #449DF5;
}

label.switch input[type="checkbox"]:checked + .slider:before {
    transform: translateX(110%);
}

label.switch .slider.round {
    border-radius: 34px;
}

label.switch .slider.round:before {
    border-radius: 34px;
}

input[type="checkbox"]:disabled + .slider {
    opacity: .5;
    cursor: auto;
    cursor: initial;
    pointer-events: none;
}

/* scss/Atoms/_Range-Slider.scss */
input[type="range"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 2px;
    border-radius: 1px;
    box-sizing: content-box;
}

input[type="range"]::-webkit-slider-thumb {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: #fff;
    height: 20px;
    width: 20px;
    border-radius: 50em;
    box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.2);
}

[type='range'] {
    --range: calc(var(--max) - var(--min));
    --ratio: calc((var(--val) - var(--min)) / var(--range));
    --sx: calc(.5 * 1.5em + var(--ratio) * (100% - 1.5em));
    margin: 0;
    padding: 0;
    width: 100%;
    height: 1.5em;
    background: transparent;
    font: 1em/1 arial, sans-serif;
}

[type='range'], [type='range']::-webkit-slider-thumb {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}

[type='range']::-webkit-slider-runnable-track {
    box-sizing: border-box;
    border: none;
    width: 100%;
    height: 0.25em;
    background: #ccc;
    border-radius: 1em;
    outline: none;
}

.js [type='range']::-webkit-slider-runnable-track {
    background: transparent;
}

[type='range']::-webkit-slider-runnable-track:focus, [type='range']::-webkit-slider-runnable-track:active {
    outline: none;
    border: none;
}

[type='range']::-moz-range-track {
    box-sizing: border-box;
    border: none;
    width: 100%;
    height: 0.25em;
    background: #ccc;
    border-radius: 1em;
    outline: none;
}

[type='range']::-moz-range-track:focus, [type='range']::-moz-range-track:active {
    outline: none;
    border: none;
}

[type='range']::-ms-track {
    box-sizing: border-box;
    border: none;
    width: 100%;
    height: 0.25em;
    background: #ccc;
    border-radius: 1em;
    outline: none;
}

[type='range']::-ms-track:focus, [type='range']::-ms-track:active {
    outline: none;
    border: none;
}

[type='range']::-moz-range-progress {
    display: block;
    background: rgba(1, 201, 234, 0.8);
}

[type='range']::-ms-fill-lower {
    display: block;
    background: rgba(1, 201, 234, 0.8);
}

[type='range']::-webkit-slider-thumb {
    margin-top: -0.625em;
    box-sizing: border-box;
    border: 1px solid rgba(255, 255, 255, 0);
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
